*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --font-family: "Roboto", sans-serif;
  --font-family-hero: "Playfair Display", serif;
  --Blue-font: #0a224a;
  --White: #fff;
}

.body-background {
  background-color: #0e0e0e;
  background-image: url("bg-mobile.38ee05aa.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
}

@media screen and (min-width: 768px) {
  .body-background {
    background-image: url("bg-tablet.6c4dce58.png");
  }
}

@media screen and (min-width: 1024px) {
  .body-background {
    background-image: url("bg-desktop.061d0975.jpg");
    background-position: right 50% bottom 0;
  }
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html, body {
  color: #0a224a;
  font-family: Roboto, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
}

h1 {
  font-family: Playfair Display, serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
  font-style: normal;
  text-decoration: none;
}

button, input, select, textarea {
  font: inherit;
  background: none;
  border: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.hero_section {
  height: 446px;
  background-image: linear-gradient(rgba(13, 13, 13, .19), rgba(14, 14, 14, .8)), url("hero-wallpaper_dark-mode.88c7d3d1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
}

.hero_section:after {
  content: "";
  width: 100%;
  height: 100%;
  opacity: 1;
  background-image: linear-gradient(rgba(13, 13, 13, .19), rgba(14, 14, 14, .8)), url("hero-wallpaper_dark-mode.88c7d3d1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: opacity .5s;
  top: 0;
  left: 0;
}

.hero_section.light:after {
  opacity: 1;
  background-image: url("hero-wallpaper_light-mode.80303c75.jpg");
}

.hero_logo {
  width: 237px;
  height: 190px;
  position: absolute;
  top: 0;
}

.hero_container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.hero_title {
  color: #dc56c5;
  text-align: center;
  width: 608px;
  height: 122px;
  margin-top: 40px;
  font-family: Montserrat;
  font-size: 50px;
  font-weight: 900;
  line-height: 1.219;
}

.hero_search-filter_container {
  justify-content: center;
  gap: 32px;
  margin-top: 48px;
  display: flex;
}

.hero-search .search {
  width: 288px;
  height: 40px;
  backdrop-filter: blur(10px);
  color: #939393;
  background-color: rgba(255, 255, 255, .2);
  border: none;
  border-radius: 20px;
  padding: 10px 60px 10px 32px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.21;
  transition: box-shadow .3s;
}

.hero-search .search:hover {
  width: 288px;
  height: 40px;
  border-radius: 20px;
  outline: none;
  box-shadow: 0 0 15px 5px #ff78d6;
}

.hero-filter .country-input:hover {
  border-radius: 20px;
  outline: none;
  box-shadow: 0 0 15px 5px #ff78d6;
}

.hero-search .search::-ms-input-placeholder {
  color: #939393;
  border-radius: 20px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.21;
}

.hero-search .search::placeholder {
  color: #939393;
  border-radius: 20px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.21;
}

.hero-search {
  position: relative;
}

.btn-search-svg {
  fill: #939393;
  transition: fill .2s, transform .2s;
  position: absolute;
  top: 14px;
  right: 32px;
}

.btn-search-svg:hover {
  fill: #ff78d6;
  transform: scale(1.5);
}

.hero-filter .country-input {
  width: 288px;
  height: 40px;
  backdrop-filter: blur(10px);
  color: #939393;
  background-color: rgba(255, 255, 255, .2);
  border-radius: 20px;
  padding: 10px 60px 10px 32px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.21;
  transition: box-shadow .3s;
}

.hero-filter .country-input::-ms-input-placeholder {
  color: #939393;
  border-radius: 20px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.21;
}

.hero-filter .country-input::placeholder {
  color: #939393;
  border-radius: 20px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.21;
}

.hero-filter {
  position: relative;
}

.btn-filter {
  position: absolute;
  top: 15px;
  right: 32px;
}

.hero-filter .country-container {
  color: #fff;
  width: 288px;
  min-height: 0;
  max-height: 165px;
  backdrop-filter: blur(20px);
  z-index: 15;
  background: rgba(255, 255, 255, .2);
  border-radius: 25px;
  display: none;
  position: absolute;
  top: 45px;
  overflow: hidden;
  box-shadow: 1px 3px 24px -5px rgba(255, 86, 197, .69);
}

.country-container.visible {
  display: block;
}

.country-item {
  text-align: left;
  color: #ccc;
  cursor: pointer;
  padding: 4px 10px 4px 30px;
  font-size: 16px;
  line-height: 19.5px;
}

.country-item:hover {
  color: #ff78d6;
}

.hero-filter .country-container .country-list {
  max-height: 165px;
  box-sizing: content-box;
  border-radius: 25px;
  padding-right: 8px;
  overflow-y: scroll;
}

.hero-filter .country-container .country-list::-webkit-scrollbar {
  width: 8px;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-track {
  background: none;
  border-radius: 25px;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-thumb {
  background-color: #939393;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 25px;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-thumb:hover {
  background-color: #ff78d6;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-button:start:increment {
  height: 12%;
  background: none;
  display: block;
}

.hero-filter .country-container .country-list::-webkit-scrollbar-button:end:increment {
  height: 12%;
  background: none;
  display: block;
}

@media (min-width: 768px) and (max-width: 1280px) {
  .hero_section {
    height: 364px;
  }

  .hero_logo {
    width: 180px;
    height: 150px;
  }

  .hero_title {
    width: 400px;
    height: 74px;
    font-size: 30px;
    line-height: 1.219;
  }

  .hero-search .search, .hero-filter .country-input {
    width: 336px;
    height: 40px;
  }

  .hero-filter .country-container {
    width: 336px;
  }

  .hero_search-filter_container {
    margin-top: 40px;
  }

  .hero-filter .country-container {
    max-height: 140px;
  }

  .hero-filter .country-container .country-list::-webkit-scrollbar-button:end:increment {
    height: 30%;
  }

  .back-to-the-top {
    display: none;
  }
}

@media (max-width: 768px) {
  .hero_section {
    height: 364px;
  }

  .hero_title {
    margin-top: 80px;
  }

  .hero_logo {
    width: 106px;
    height: 90px;
  }

  .hero_title {
    width: 275px;
    height: 44px;
    font-size: 18px;
    line-height: 1.219;
  }

  .hero-search .search, .hero-filter .country-input {
    width: 280px;
    height: 40px;
  }

  .hero-filter .country-container {
    width: 280px;
  }

  .hero_search-filter_container {
    flex-direction: column;
    align-items: center;
    gap: 24px;
    margin-top: 24px;
    display: flex;
  }

  .hero-filter .country-container {
    max-height: 80px;
  }

  .hero-filter .country-container .country-list::-webkit-scrollbar-button:end:increment {
    height: 65%;
  }

  .back-to-the-top {
    display: none;
  }
}

.back-to-the-top {
  width: 70px;
  height: 70px;
  opacity: 0;
  z-index: 100;
  cursor: pointer;
  background-color: #ff78d6;
  border: 3px solid #ff78d6;
  border-radius: 50%;
  transition: opacity .3s;
  position: fixed;
  bottom: 60px;
  right: 30px;
}

.back-to-the-top_svg {
  fill: #fff;
}

.hero_theme-toggle {
  width: 50px;
  height: 50px;
  cursor: pointer;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .3s;
  display: flex;
  position: absolute;
  top: 30px;
  right: 50px;
}

.hero_theme-toggle[data-theme="dark"], .hero_theme-toggle[data-theme="light"] {
  background-color: #000;
}

.theme-toggle_light-icon {
  width: 24px;
  height: 24px;
  fill: #fff;
  display: block;
}

.theme-toggle_dark-icon {
  width: 24px;
  height: 24px;
  fill: #fff;
  display: none;
}

.hero_theme-toggle[data-theme="dark"] .theme-toggle_dark-icon {
  display: block;
}

.hero_theme-toggle[data-theme="dark"] .theme-toggle_light-icon, .hero_theme-toggle[data-theme="light"] .theme-toggle_dark-icon {
  display: none;
}

.hero_theme-toggle[data-theme="light"] .theme-toggle_light-icon {
  display: block;
}

.hero_theme-toggle:hover .theme-toggle_light-icon {
  fill: #ffd900;
}

.hero_theme-toggle:hover .theme-toggle_dark-icon {
  fill: #5a00c1;
}

p, span {
  color: #0e0e0e;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#event-cards {
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin: 0 auto;
  padding: 0;
  display: grid;
}

.event-card {
  width: 100%;
  min-height: 208px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  transition: transform .3s ease-in-out;
  position: relative;
}

.event-card:hover, .event-card:focus {
  cursor: pointer;
  transform: scale(1.1);
}

.event-card:after {
  content: "";
  width: 102px;
  height: 95px;
  z-index: 1;
  border: 2px solid rgba(220, 86, 197, .3);
  border-radius: 50px 0;
  position: absolute;
  top: 0;
  left: 30px;
}

.event-card-image {
  width: 120px;
  height: 120px;
  object-fit: cover;
  box-shadow: -25px -10px 15px -25px var(--cloud);
  border-radius: 50px 0;
  margin-top: 10px;
  display: block;
}

.event-card-name {
  color: #dc56c5;
  margin: 5px 0;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.event-card-date {
  color: #fff;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.event-card-place {
  color: #fff;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 600;
  position: relative;
}

.event-card-place:before {
  content: "";
  width: 12px;
  height: 16px;
  vertical-align: middle;
  background-image: url("data:image/svg+xml;utf8,<svg fill='%23f5f4f4' version='1.1' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 425.963 425.963'><path d='M213.285,0h-0.608C139.114,0,79.268,59.826,79.268,133.361c0,48.202,21.952,111.817,65.246,189.081c32.098,57.281,64.646,101.152,64.972,101.588c0.906,1.217,2.334,1.934,3.847,1.934c0.043,0,0.087,0,0.13-0.002c1.561-0.043,3.002-0.842,3.868-2.143c0.321-0.486,32.637-49.287,64.517-108.976c43.03-80.563,64.848-141.624,64.848-181.482C346.693,59.825,286.846,0,213.285,0z M274.865,136.62c0,34.124-27.761,61.884-61.885,61.884c-34.123,0-61.884-27.761-61.884-61.884s27.761-61.884,61.884-61.884C247.104,74.736,274.865,102.497,274.865,136.62z'/></svg>");
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 8px;
  display: inline-block;
}

.booster {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: -60px;
  display: flex;
}

.notfound {
  height: 40vh;
  color: var(--cloud);
  text-align: center;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: 400;
  display: none;
}

@media screen and (min-width: 768px) {
  #event-cards {
    grid-template-columns: repeat(4, 1fr);
    row-gap: 52px;
  }

  .event-card {
    min-height: 211px;
  }

  .event-card:after {
    width: 120px;
    height: 112px;
    top: -5px;
    left: 30px;
  }

  .event-card-image {
    width: 141px;
    height: 120px;
  }

  .event-card-place:before {
    width: 10px;
    height: 15px;
  }

  .booster {
    margin-top: -90px;
  }
}

@media screen and (min-width: 1158px) {
  #event-cards {
    grid-template-columns: repeat(4, 1fr);
    row-gap: 40px;
  }

  .event-card {
    width: 288px;
    min-height: 320px;
  }

  .event-card:after {
    width: 227px;
    height: 213px;
    top: -10px;
    left: 50px;
  }

  .event-card-image {
    width: 267px;
    height: 226.244px;
  }
}

.event-modal-overlay {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(3px);
  z-index: 999;
  background-color: rgba(32, 13, 29, .6);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.event-modal {
